import * as React from 'react';
import { View, StyleSheet } from 'react-native';
import Icon from '../Icon';
/**
 * A component to show an icon in a list item.
 *
 * ## Usage
 * ```js
 * import * as React from 'react';
 * import { List, Colors } from '../../';
 *
 * const MyComponent = () => (
 *   <List.Icon color={Colors.blue500} icon="folder" />
 * );
 *
 * export default MyComponent;
 * ```
 */
let ListIcon = /** @class */ (() => {
    class ListIcon extends React.Component {
        render() {
            const { icon, color: iconColor, style } = this.props;
            return (<View style={[styles.item, style]} pointerEvents="box-none">
        <Icon source={icon} size={24} color={iconColor}/>
      </View>);
        }
    }
    ListIcon.displayName = 'List.Icon';
    return ListIcon;
})();
export default ListIcon;
const styles = StyleSheet.create({
    item: {
        margin: 8,
        height: 40,
        width: 40,
        alignItems: 'center',
        justifyContent: 'center',
    },
});
